import React, { useState } from 'react';
import { Input } from '@components/libs/Forms';
import Button from '@components/libs/Button';
import SojiBaseInit from '@modules/SojiBase';
import styled from 'styled-components';

import Company from '../components/Company';
import Disclaimer from '../components/Disclaimer';
import Scammed from '../components/Scammed';

const SendButton = styled(Button)`
    width: 100%;
    height: 40px;
`;


const validateEmail = (email) => {
    return String(email)
      .toLowerCase()
      .match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      );
  };

export default function SearchEmail() {
    const [state, setState] = useState({
        email: '',
        response: false,
        onRequest: false,
        noInfo: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const searchemail = async () => {
        stateHandler('noInfo', false);
        stateHandler('response', false);
        if (!validateEmail(state.email)) {
            stateHandler('noInfo', 'Please enter valid email address');
            return;
        }
        if (state.email == '') {
            stateHandler('noInfo', "Email Address textbox can't be empty");
            return;
        }
        stateHandler('onRequest', true);
        const SojiBase = await SojiBaseInit;
        const requestPayload1 = { email: state.email };
        const getter = await SojiBase.post('v1/search_engine/search_emails', requestPayload1);
        getter.on('static', async (response) => {
            stateHandler('onRequest', false);
            const data = response.response.data;
            if (data.length > 0) {
                stateHandler('response', data);
            } else {
                stateHandler('noInfo', 'No Information Yet');
            }
        });
    };

    return (
        <>
            <Input placeholder="Paste your E-mail address here for verification" onChange={(e) => stateHandler('email', e.target.value)} />
            {state.response &&
                state.response.map((result) => {
                    if (result.isScam == 'Yes') {
                        return <Scammed result={result} />;
                    } else if (result.isScam == 'No') {
                        return <Company result={result} />;
                    } else if (result.isScam == 'Disclaimer') {
                        return <Disclaimer result={result} email />;
                    } else if (result.isScam == 'No Token') {
                        return <p>{result.description}</p>;
                    } else {
                        return <p>No Information yet</p>;
                    }
                })}
            {state.noInfo && <p>{state.noInfo}</p>}
            <div className="button_container">
                <SendButton
                    title="Search"
                    className="button"
                    loading={state.onRequest}
                    onClick={() => searchemail()}
                />
            </div>
        </>
    );
}
