import React, { useState, useEffect } from 'react';
import Structure from '../Structure';
import SearchPhoneNumber from './SearchPhoneNumber';
import SearchEmail from './SearchEmail';
import SearchText from './SearchText';
import mixpanel from '@components/mixpanel';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const AgreementBox = styled.div`
    margin-top: 10px;
    font-size: 13px;
`;

const Links = styled(Link)`
    color: ${colors.defaultColor}
`

import './index.css';
import { colors } from '../../../components/typo';

export default function HomePage() {
    const [page, setPage] = useState('documents');

    useEffect(() => {
        //mixpanel
        mixpanel.track('Sojiare User Search', {
            'Opted out of email': true,
            'ip': '0',
        });
    }, []);

    const Description = () => {
        return (
            <div>
                <div className="desc">Scan for FRAUDULENT Documents, Phone Number and Email</div>
                <AgreementBox>
                    By proceeding, I agree to Sojiare <Links to="/terms-and-conditions">Terms of Use</Links> and acknowledge that I have read the <Links to="/policy">Privacy Policy</Links>.
                </AgreementBox>
            </div>
        );
    };

    return (
        <>
            <Structure description={<Description />}>
                <div className="tab_container">
                    <div
                        className={`tab_button ${page == 'documents' && 'active'}`}
                        onClick={() => setPage('documents')}
                    >
                        Documents
                    </div>
                    <div
                        className={`tab_button ${page == 'phoneNumber' && 'active'}`}
                        onClick={() => setPage('phoneNumber')}
                    >
                        Phone Number
                    </div>
                    <div className={`tab_button ${page == 'email' && 'active'}`} onClick={() => setPage('email')}>
                        Email
                    </div>
                </div>
                {page == 'documents' && <SearchText />}
                {page == 'phoneNumber' && <SearchPhoneNumber />}
                {page == 'email' && <SearchEmail />}
            </Structure>
        </>
    );
}
