import React, { useState } from 'react';
import styled from 'styled-components';
import { GrFlagFill } from 'react-icons/gr';
import { MdVerifiedUser } from 'react-icons/md';
import { useAlertState } from '@components/libs/Notify/useAlertState';
import Alert from '@components/libs/Alert';

const Title = styled.p`
    font-weight: bold;
`;

const Button = styled.button`
    padding: 10px;
    border-radius: 5px;
    box-shadow: rgb(0 0 0 / 5%) 0px 0px 20px;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 5px;
`;

const Container = styled.div`
    width: 100%;
    display: flex;
`;
const IconBox = styled.div`
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
`;
const ActionCardWrapper = styled.div`
    flex: 1;
    text-align: left;
    margin-left: 10px;
`;
const ActionCardTitle = styled.div`
    font-weight: 600;
`;
const ActionCardDesc = styled.div``;

export default function Disclaimer({ result, phoneNumber }) {
    const alertState = useAlertState();
    const [state, setState] = useState({
        openFlagModal: false,
        openVerifyModal: false,
        buttonLoading: false,
    });

    console.log(result);

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    return (
        <>
            <Title>{result.description}</Title>

            <Button onClick={() => stateHandler('openFlagModal', true)}>
                <Container>
                    <IconBox>
                        <GrFlagFill size={24} />
                    </IconBox>
                    <ActionCardWrapper>
                        <ActionCardTitle>Flag</ActionCardTitle>
                        <ActionCardDesc>
                            Flag {result.searchedContent} as a fraudulent {phoneNumber ? 'Phone Number' : 'Email'}
                        </ActionCardDesc>
                    </ActionCardWrapper>
                </Container>
            </Button>

            <Button onClick={() => stateHandler('openVerifyModal', true)}>
                <Container>
                    <IconBox>
                        <MdVerifiedUser size={24} />
                    </IconBox>
                    <ActionCardWrapper>
                        <ActionCardTitle>Verify</ActionCardTitle>
                        <ActionCardDesc>
                            Confirm {result.searchedContent} as a verified {phoneNumber ? 'Phone Number' : 'Email'}
                        </ActionCardDesc>
                    </ActionCardWrapper>
                </Container>
            </Button>

            <Alert
                visible={state.openFlagModal}
                setModal={() => stateHandler('openFlagModal', false)}
                actionDescription={`Are you sure you want to flag ${result.searchedContent} as a Scam?`}
                onActionButtonClick={() => null}
                buttonLoading={state.buttonLoading}
                actionButtonText="Okay, Add"
            />

            <Alert
                visible={state.openVerifyModal}
                setModal={() => stateHandler('openVerifyModal', false)}
                actionDescription={`Are you sure you want to add ${result.searchedContent} as a verified ${
                    phoneNumber ? 'Phone Number' : 'Email'
                }?`}
                onActionButtonClick={() => null}
                buttonLoading={state.buttonLoading}
                actionButtonText="Okay, Add"
            />
        </>
    );
}
